	module.exports = {"data":{"markdownRemark":{"html":"<p>What is troubling me?</p>\n<p>Am I struggling with wanting to change something and can’t get perfection?</p>\n<p>Don’t let perfection be the enemy of better.</p>\n<p>It’s about progress, not perfection.</p>\n<p>What’s your goal?</p>\n<p>Can you take a small or several small steps in the desired direction?</p>\n<p>Change can happens little by little, just like habits you build over time.</p>\n<p>Sometimes you need to slow down, that’s okay.</p>\n<p><em>In order to do any of this, you need to take care of yourself first.  Everything else can wait.</em></p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-0a734.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 700px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 149.98783158919446%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"IMG 2194\"\n        title=\"\"\n        src=\"/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-55fca.jpg\"\n        srcset=\"/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-856e4.jpg 175w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-91e36.jpg 350w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-55fca.jpg 700w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-b1f1f.jpg 1050w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-065e1.jpg 1400w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-d0dab.jpg 2100w,\n/static/IMG_2194-53e8d51c55c6d3955a9c89804489891e-0a734.jpg 4109w\"\n        sizes=\"(max-width: 700px) 100vw, 700px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>Here are ways I change my state:</h2>\n<p>Have you eaten in the last 3 hours?</p>\n<p>Have you showered today?</p>\n<p>Have you gotten dressed?</p>\n<p>Have you moved your body <em>in a way that feels good</em>?</p>\n<p>Do you need a nap?</p>\n<p>Go get some sunshine.</p>\n<p>Practice gratitude..</p>\n<ul>\n<li>For how far you’ve come</li>\n<li>For the many privileges you have</li>\n<li>For the blessings of your current life</li>\n<li>For your health</li>\n<li>For your relationships</li>\n<li>For your passions</li>\n<li>For the many ways you’ve grown</li>\n<li>For the ways you’ve contributed to others</li>\n<li>For yourself, and the things you like about yourself</li>\n</ul>","frontmatter":{"date":"July 31, 2018","path":"/blog/upset","title":"When I feel stuck and upset"}}},"pathContext":{}}

